.sidebarChannel:hover > h4 {
    color: white;
    background-color: #40464b;
    border-radius: 6px;
}

.sidebarChannel > h4 {
    display: flex;
    padding-left: 15px;
    margin-bottom: 2px;
    align-items: center;
    background-color: #2f3135;
    color: #8E9297;
    cursor: pointer;
    height: 45px;
}

.sidebarChannel__hash {
    font-size: 30px;
    padding: 8px;
}
