.sidebar {
    display: flex;
    flex-direction: column;
    flex: 0.15;
    height: 100vh;
    background-color: #2F3136;
}

.sidebar__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #2F3136;
    color: rgb(207, 207, 207);
    border-bottom: 3px solid #26282c;
}

.sidebar__channels {
    flex: 1;
}

.sidebar__addChannel {
    cursor: pointer;
}

.sidebar__addChannel:hover {
    color: white;
}

.sidebar__channelsHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #2F3136;
    color: #8E9297;
}

.sidebar__header {
    display: flex;
    align-items: center;
}

.sidebar__voice,
.sidebar__profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #8E9297;
    background-color: #2F3136;
    padding: 10px;
    border: 1px solid #8E9297;
}

.sidebar__voiceInfo {
    flex: 1;
    padding: 10px;
}

.sidebar__voiceIcon {
    color: #4fb185;
}

.sidebar__voiceInfo > h3 {
    color: #4fb185;
}

.sidebar__voiceInfo > p {
    font-size: smaller;
}

.sidebar__voiceIcons > .MuiSvgIcon-root {
    padding: 5px;
}

.sidebar__profileIcons > .MuiSvgIcon-root {
    padding: 5px;
    cursor: pointer;
}

.sidebar__profileIcons > .MuiSvgIcon-root:hover {
    color: white;
}

.sidebar__profileInfo {
    flex: 1;
    padding: 10px;
}

.sidebar__profileInfo > h3 {
    color: white;
}

.sidebar__profileInfo > .MuiSvgIcon-root {
    padding: 10px;
}
